import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home'
import Pandora from './views/Pandora/Pandora'
import AirLineDetailPage from './views/AirLineDetailPage'
import LoginModal from './components/LoginModal'
import GuideMyPage from './views/GuideMyPage'
import MyPage from './views/MyPage'

Vue.use(Router)

export default new Router({
    mode: 'history',
    // base: process.env.BASE_URL,
    routes: [
        // -------------------------------------------
        // 2 
        {
            //   .
            path: '/',
            name: 'Home',
            component: Home
        },
        {   // 1     .
            path: '/pandora',
            name: 'Pandora',
            component: Pandora
        },
        {
            path: '/mypage',
            name: 'Mypage',
            component: MyPage
        },
        {
            path: '/guidemypage',
            name: 'GuideMyPage',
            component: GuideMyPage
        },
        {
            path: '/login',
            name: 'LoginModals',
            component: LoginModal
        },
        {   
            path: '/airlineDetail',
            name: 'AirLineDetailPage',
            component: AirLineDetailPage
        },
    ]
})
